<template>
    <van-nav-bar
        :left-text="leftText"
        :title="title"
        :right-text="rightText"
        left-arrow
        :border="false"
        class="nong--navbar"
        @click-left="rtn"
        @click-right="go2"/>
</template>

<script>
import { NavBar } from 'vant'
export default {
    name: 'navBar',
    components: {
        [NavBar.name]: NavBar
    },
    props: {
        leftText: {
            type: String,
            default: '返回'
        },
        title: {
            type: String,
            default: ''
        },
        rightText: {
            type: String,
            default: ''
        }
    },
    methods: {
        rtn() {
            this.$route.name !== 'home' && this.$router.go(-1)
        },
        go2() {
            this.$emit('finish')
        }
    }
}
</script>

<style lang="less">
.nong--navbar {
    background: transparent;
    .van-icon-arrow-left {
        color: @ft-color-ssg;
        &:active {
            opacity: .8;
        }
    }
    .van-nav-bar__text {
        color: @ft-color-ssg;
        font-size: @base-size * 1.2;
        &:active {
            background-color: transparent;
            opacity: .8;
        }
    }
    .van-nav-bar__right {
        .van-nav-bar__text {
            color: @base-color;
        }
    }
}
</style>
